.navBar{
    grid-area: n;
    position: relative;
    width: 100%;
    height: 57px;  
    background: #F9FAFB;
}
.navBar::after{
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background: #E4E7EB;
}

.navBar_items{
    vertical-align: top;
    width: 314px;
    height: 38px;
    left: 294px;
    top: 83px;
    padding-left: 24px;
    padding-top: 19px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.navBar_items a{
    text-decoration: none;
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: -0.05px;
    color: #3A3B3F;
}

